.landingZone {
  height: 100vh;
  position: relative;
  display: grid;
  place-items: center;
  background-color: #484E60;
}
.landingZone::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  clip-path: polygon(0 0, 100% 0, 100% 80%, 50% 95%, 0 80%);
  z-index: 1;
}
@media (max-width: 500px) {
  .landingZone::before {
    clip-path: polygon(0 0, 100% 0, 100% 90%, 50% 95%, 0 90%);
  }
}

.background {
  width: 100%;
  height: 100%;
  background-image: url(../assets/mountain.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.2;
  position: absolute;
  z-index: 2;
  clip-path: polygon(0 0, 100% 0, 100% 80%, 50% 95%, 0 80%);
}
@media (max-width: 500px) {
  .background {
    clip-path: polygon(0 0, 100% 0, 100% 90%, 50% 95%, 0 90%);
  }
}

.overlay {
  z-index: 3;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 70vh;
}

.socialButtons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 4rem;
}

.logo {
  width: 50%;
  height: 100%;
  background-image: url(../assets/twentyfivesoftware-logo.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center bottom;
}
@media (max-width: 1200px) {
  .logo {
    width: 60%;
  }
}
@media (max-width: 900px) {
  .logo {
    width: 70%;
  }
}
@media (max-width: 700px) {
  .logo {
    width: 80%;
  }
}

.icon {
  position: absolute;
  top: 2rem;
  left: 4rem;
  width: 7rem;
  z-index: 3;
}

.angle {
  position: absolute;
  left: 50%;
  bottom: 10%;
  font-size: 7rem;
  text-decoration: none;
  transform: translateX(-50%);
  color: #484E60;
  animation: bounce 2s infinite;
  z-index: 3;
  outline: none;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translate(-50%, 0);
  }
  40% {
    transform: translate(-50%, -3rem);
  }
  60% {
    transform: translate(-50%, -1.5rem);
  }
}
.socialButton {
  font-size: 4rem;
  text-decoration: none;
  color: #484E60;
  margin: 0 2rem;
  transition: 0.4s linear;
  position: relative;
  width: 7rem;
  height: 7rem;
  display: grid;
  place-items: center;
}
.socialButton::before, .socialButton::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  transition: 0.4s linear;
  box-sizing: border-box;
}
.socialButton:hover {
  transform: scale(0.8);
}
.socialButton:hover::before {
  border-left: 0.4rem solid #484E60;
  border-right: 0.4rem solid #484E60;
  transform: skewX(20deg);
}
.socialButton:hover::after {
  border-top: 0.4rem solid #484E60;
  border-bottom: 0.4rem solid #484E60;
  transform: skewY(-20deg);
}
@media (max-width: 550px) {
  .socialButton {
    margin: 0 1rem;
    font-size: 3rem;
  }
}

.projectsZone {
  padding: 8rem 5rem;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #484E60;
}

.title {
  text-align: center;
  font-weight: 900;
  font-size: 5rem;
  color: #ffffff;
  letter-spacing: 0.15rem;
  line-height: 1.2;
}

.subtitle {
  text-align: center;
  font-weight: 500;
  color: #bdbfc7;
  font-size: 2.2rem;
  line-height: 1;
  margin-top: 0.5rem;
}
@media (max-width: 750px) {
  .subtitle {
    max-width: 75vw;
  }
}
.subtitle a {
  text-decoration: none;
  color: inherit;
}

.list {
  padding-top: 10rem;
}

.footer {
  height: 11rem;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 550px) {
  .footer {
    height: 8rem;
  }
}