.button, .button__iconRight {
  width: max-content;
  padding: 0.8rem 2rem 0.8rem 0;
  border-radius: 1.2rem;
  font-size: 1.8rem;
  text-decoration: none;
  margin-right: 2rem;
  border: 0.3rem solid;
  user-select: none;
  display: flex;
  align-items: center;
}
@media (max-width: 550px) {
  .button, .button__iconRight {
    margin-right: 0;
    margin-bottom: 1rem;
  }
}
.button svg, .button__iconRight svg {
  margin: 0 1.5rem;
}

.button__iconRight {
  padding: 0.8rem 0 0.8rem 2rem;
}